import React from 'react'
import styled from 'styled-components'
import {WorkCard} from 'components/Cards'
import centilCover from 'images/centilCover.jpg'
import agromirCover from 'images/agromirCover.jpg'
import lpCover from 'images/lpCover.jpg'
import rhythmCover from 'images/workCover.jpg'
import {mediaQueries} from 'constants/mediaQueries'
import useQuery from 'helpers/hooks/useQuery'

const Wrap = styled('div')`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 0 30px;
  & > *:nth-child(even) {
    margin-top: 30px;
  }
  @media ${mediaQueries.tabletS}{
    grid-template-columns: 1fr;
    grid-gap: 30px 0;
    & > *:nth-child(even) {
      margin-top: 0;
    }
  }
  @media ${mediaQueries.mobileL}{
    grid-gap: 10px 0;
  }
`

const Works = () => {
  // useQuery
  const goImageViewer = useQuery({
    queries: {
      imageViewer: 'helloWorld'
    }
  })

  return (
    <Wrap>
      <WorkCard
        title={'Rhythm Mobile ERP'}
        subtitle={'ReactJS PWA'}
        year={'2019'}
        img={rhythmCover}
        withDetail
        onLink={() => goImageViewer()}
      />
      <WorkCard
        title={'Cotton picking monitoring'}
        subtitle={'ReactJS'}
        year={'2019'}
        img={agromirCover}
      />
      <WorkCard
        title={'Trainings Forum for Centil'}
        subtitle={'ReactJS'}
        year={'2019'}
        img={centilCover}
      />
      <WorkCard
        title={'Rhythm LP'}
        subtitle={'ReactJS'}
        year={'2019'}
        img={lpCover}
      />
    </Wrap>
  )
}

export default Works
